import { useDispatch, useSelector } from 'react-redux';
import { getChecklistGlobalConfig } from 'state/readers/checklist';
import { useMemo } from 'react';
import { toggleFullscreenMode } from 'state/actions/checklist';

export const useChecklistFullscreenMode = () => {
    const dispatch = useDispatch();

    const { fullscreenMode } = useSelector(getChecklistGlobalConfig);

    const toggleFullscreenModeHandler = useMemo(
        () => () => dispatch(toggleFullscreenMode()),
        [dispatch],
    );

    return { fullscreenMode, toggleFullscreenModeHandler };
};
